<template>
    <div class="container">
      <el-row>
        <el-col :span="8">
          <h1 class="title">修改翻译需求</h1>
          <label for="">任务名</label><el-input v-model="task.task_name" placeholder="请输入任务名"></el-input>
          <label for="">翻译语言</label>
          <el-select v-model="task.translation_language" placeholder="请选择">
            <el-option
              v-for="item in languageOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <label for="">紧急程度</label>
          <el-select v-model="task.urgency_degree" placeholder="请选择">
            <el-option
              v-for="item in degreeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <label for="">要求</label>
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入内容"
            v-model="task.requirement">
          </el-input>
          <div class="button">
            <el-upload
              class="upload-img"
              action="http://img.orkotech.com/fdfs_upload.php"
              :data="uploadParam"
              :before-upload="beforeUpload"
              :on-success="uploadSuccess"
              :on-error="uploadFail"
              ref="upload"
              multiple>
              <el-button size="small" type="primary">上传附件</el-button>
            </el-upload>
          </div>
        </el-col>
      </el-row>
      <div class="footerButton border-shadow">
        <el-button type="primary" @click="submitTask()" style="vertical-align: top">提交</el-button>
      </div>
    </div>
</template>

<script>
    export default {
      name: "updateTask",
      data() {
          return {
            task: {
              task_name: "",
              translation_language: "",
              urgency_degree: "",
              requirement: "",
              content_url: ''
            },
            uploadParam: {
              file_field: "file",//文件上传字段 将文件流保存到该值（eg:fdfs_file）
              token: "",//加密token md5( 加密salt值 + 13位时间戳 )
              time: +new Date(),//13位时间戳
              format: "",//上传的文件格式
              file: null//文件流
            },
            languageOptions: [
              {
                label: '简体中文',
                value: 'CN'
              },
              {
                label: '繁体中文-香港',
                value: 'HK'
              },
              {
                label: '繁体中文-台湾',
                value: 'TW'
              },
              {
                label: '英语',
                value: 'EN'
              },
              {
                label: '印尼语',
                value: 'ID'
              },
              {
                label: '泰语',
                value: 'TH'
              },
              {
                label: '马来语',
                value: 'MY'
              },
              {
                label: '阿拉伯语',
                value: 'UA'
              }
            ],
            degreeOptions: [
              {
                label: '高',
                value: '2'
              },
              {
                label: '中',
                value: '1'
              },
              {
                label: '低',
                value: '0'
              }
            ]
          }
      },
      methods: {
        submitTask() {
          let url = "/v1/translations/"+this.task.task_id;
          let params = {};
          params.title = this.task.task_name;//翻译任务标题
          params.uid = 2;//创建任务用户id
          params.t_language = this.task.translation_language;//翻译语言（eg:: HK TH）
          params.level = this.task.urgency_degree;//任务紧急程度（0低级 1中级 2高级）
          params.remark = this.task.requirement;//任务备注
          params.fix_uid = 2356;//任务完成人员uid
          params.status = 0;//翻译状态（0提交任务 1翻译完成 2审核翻译 3不通过再翻译 4完成翻译
          params.content_url = this.task.content_url;//翻译文案
          params.is_del = 0;
          let that = this;
          this.$axios.put(url,params).then((res) => {
            that.$message({
              type: 'success',
              message: '更新成功!'
            });
            that.$refs.upload.clearFiles();
            that.task.task_name = '';
            that.task.translation_language = '';
            that.task.urgency_degree = '';
            that.task.requirement = '';
            that.task.content_url = '';
          }).catch((err) => {
            that.$message({
              type: 'error',
              message: '更新失败!'
            });
          })
        },
        beforeUpload(file) {
          this.uploadParam.token = this.$md5('salt001'+this.uploadParam.time.toString())
          let fileName = file.name;
          this.uploadParam.format = fileName.slice(fileName.lastIndexOf('.')+1);
          this.uploadParam.file = file;
        },
        uploadSuccess(response, file, fileList) {
          if(response.status==0){
            this.task.content_url = "http://img.orkotech.com/" + response.path
          }else{
            this.$message.error(response.msg);
          }
        },
        uploadFail(err, file, fileList) {
          this.$message.error(err);
        }
      },
      created() {
        let row = this.$route.query;
        if(row.task_name){
          this.task.task_id = row.task_id;
        }
        this.task.task_name = row.task_name
        let translation_language = ''
        this.languageOptions.forEach((option) => {
          if(option.label === row.translation_language){
            translation_language = option.value;
          }
        });
        this.task.translation_language = translation_language
        let urgency_degree = ''
        this.degreeOptions.forEach((option) => {
          if(option.label === row.urgency_degree){
            urgency_degree = option.value;
          }
        });
        this.task.urgency_degree = urgency_degree
        this.task.requirement = ''
        this.task.content_url = ''
      }
    }
</script>

<style scoped lang="stylus">
  .container
    .el-row
      margin 30px 30px 0px 30px
    label
      display block
      font-weight bold
      font-size: 20px
      margin: 10px 0
    .title
      margin-bottom: 50px
    .button
      margin-top: 20px
    .upload-img
      display inline-block
      margin-right 30px
    .footerButton
      position absolute
      left: 0
      bottom 0
      width: 100%
      padding 13px 30px
      box-sizing border-box
      background-color: hsla(0,0%,100%,.9);
      z-index 2
      button
        float: right;

</style>



